body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
  overflow-y: scroll;
}
::-webkit-scrollbar {
  width: 0px;  /* Remove scrollbar space */
  background: transparent;  /* Optional: just make scrollbar invisible */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{
  background-color: white;
  overflow-x: hidden;
  overflow-y: scroll;
}
.line{
  border-bottom: 1px solid white;
}

.main-intro{
    height: 55vh;
    width: 100vw;
    
}
.intro-text{
    font-family: 'Montserrat', sans-serif;
    font-weight: bold;
    height: 55vh;
    width: 70vw;
    background-color: black;
    color: white;
    padding-top: 17%;
    padding-left: 5%; 
    overflow-wrap: break-word; 
}
.intro-img{
    position: relative;
    left: 55%;
    top:-150%; 
}
#me{
    height:80vh;
    width:30vw; 
}
.intro-text-1
{
    font-size: 50px;
}
.intro-text-2
{
    font-size: 25px;
    word-wrap: normal;
}
.navbar{
    background-color: black;
    color: white;
    height: 10vh;
    padding-top: 1%;
    padding-left: 1%;
}
button{
    margin: 2%;
    font-size: 20px;
    background-color: black;
    color: white;
    border: 1px solid black;
}
.home-main{
    height: 90vh;
    width: 100vw;
    background-color: white;  
    margin-top: 22%;  
}
.home{
    display: flex;
}
.home-head{
    color: black;
    font-size: 40px;
    font-family: 'Montserrat', sans-serif;
    text-align: center;
    margin: 3%;
    font-weight: bolder;

}
.home-text{
    color: black;
    font-family: 'Montserrat', sans-serif;
    width:50vw;
    padding-left: 5%;
    padding-right: 5%;
    flex: 60% 1;
    word-wrap: normal;
    overflow-wrap: normal;
}
.home-text-1{
    font-weight: bold;
    font-size: 30px;
    text-align: center;
}
.home-text-2{
    font-size: 20px;
    font-weight: 600;
}
#back{
    height: 60vh;
    width: 40vw;
    border: 2px solid white;
    border-radius: 10px;
}
.home-img{
    flex:40% 1;
    margin-left: 5%;
}

.projects-main{
    background-color: white;
    height:auto;
    width: 100vw;
    padding-top: 3%;
    
}
.projects-head{
    color: black;
    font-size: 40px;
    font-family: 'Montserrat', sans-serif;
    text-align: center;
    font-weight: bolder;
    padding-bottom: 1%;
    
}
.project{
   display: flex;
   align-items: center;
   border: solid #f3f1f1;
   border-radius: 30px;
   font-family: 'Montserrat', sans-serif;
   box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.25);
   margin: 3% 10%;
}
.project-text{
    padding: 3%;; 
    flex:60% 1; 
    font-size: 35px;
}
.project-name{
    font-weight: 600;
}
.project-desc{
    font-size: 22px;
    margin: 2% 0;
}
.project-img{
    flex:40% 1;
    align-items: center;
}
.pro-img{
    border: 1px solid white;
    border-radius: 8px;
    margin:1%;
}
a{
    text-decoration: none;
    font-size: 20px;
    padding: 2%;
    border: none;
    border-radius: 30px;
    box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.25);
    margin-top: 3%;
    color: purple;
}
a.visited{
    color:purple;
}
a:hover{
    color:rgb(73, 1, 73);
    box-shadow: 10px 10px 20px rgba(0, 0, 0, 0.25);
}
